* {
  margin: 0;
  padding: 0;
  line-height: 1.5;
}


/* 변수로 색상 지정 */
:root {
  --baseColor: white;
  /*주조색(배경색) - 약 70% 사용*/
  --dominantColor: black;
  /*보조색 - 약 20% 사용*/
  --accentColor: tomato;
}

h1 {
  color: var(--accentColor);
  background-color: var(--dominantColor);
  line-height: 2;
  text-align: center;
}

section {
  margin-top: 3em;
}

h2 {
  text-align: center;
  font-size: 1em;
  height: 2em;
  color: var(--accentColor);
  margin-top: 1em;
}

article {
  padding: 1em;
}

h3 {
  margin-bottom: 1em;
}

article ul,
article ol,
article > p {
  padding-left: 2em;
}

.overview {
  padding: 0.5em;
}

h4 {
  height: 2em;
}

.tip {
  padding: 0.5em 1em;
}

h5 {
  font-size: 1em;
}

table {
  border-collapse: collapse;
  border-spacing: none;
  width: 100%;
}

caption {
  text-align: left;
  height: 2em;
}

th,td {
  border: 0.1em solid var(--dominantColor);
  padding: 0.5em;
}

tbody th {
  text-align: left;
}

.del {
  color: #ddd;
}